@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "Comfortaa";
  font-display: swap;
  src: url("../fonts/Comfortaa-Regular.woff2") format("woff2"), url("../fonts/Comfortaa-Regular.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 500;
  font-family: "OpenSans";
  font-display: swap;
  src: url("../fonts/OpenSans-Medium.woff2") format("woff2"), url("../fonts/OpenSans-Medium.woff") format("woff");
}
:root {
  --body-bg: #000000;
  --body-font-size: 16px;
  --content-width: 1710px;
  --container-offset: 15px;
  --container-width: calc(var(--content-width) + (var(--container-offset) * 2));
  --header-height: 49px;
  --scrollbar-thumb: #000000;
  --scrollbar-thumb-hover: #000000;
  --scrollbar-track: #474747;
  --scrollbar-track-hover: #474747;
  --overlay-bg: rgba(0, 0, 0, 0.5);
  --font-family: "Comfortaa" , sans-serif;
  --font-family2: "OpenSans" , sans-serif;
  --burger-cl: 0, 214, 85;
  --cl-a: 255, 255, 255;
  --cl-b: 0, 0, 0;
  --cl-c: 181, 181, 181;
  --cl-d: 107, 107, 107;
  --cl-e: 167, 167, 167;
  --bg-a: 255, 255, 255;
  --bg-b: 0, 0, 0;
  --bg-c: 211, 211, 211;
  --bg-d: 25, 25, 25;
  --bg-e: 72, 72, 72;
  --bg-f: 99, 99, 99;
  --bg-g: 82, 82, 82;
  --bg-h: 19, 19, 19;
  --bg-i: 99, 99, 99;
  --bg-j: 167, 167, 167;
  --br-a: #474747;
}
@media (max-width: 1024px) {
  :root {
    --header-height: 49px;
  }
}
@media (max-width: 767px) {
  :root {
    --header-height:108px;
  }
}
@media (max-width: 576px) {
  :root {
    --header-height:108px;
  }
}

