header {
  position: fixed;
  top: 0;
  width: 100%;
  z-index: 10;
}

.header {
  width: 100%;
}

.header-box {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  grid-gap: 40px;
}
@media (max-width: 767px) {
  .header-box {
    justify-content: space-between;
    padding: 30px 0;
  }
}
.header-box__inner {
  display: flex;
}
@media (max-width: 767px) {
  .header-box__inner {
    display: none;
  }
}

footer {
  background-color: rgba(var(--bg-h), 1);
}

.footer-inner {
  padding: clamp(75px, 6.926vw, 80px) 0 clamp(75px, 10.996vw, 127px);
  display: flex;
  align-items: center;
  justify-content: center;
}
.footer__title {
  color: rgba(var(--bg-i), 1);
  font-family: var(--font-family);
  font-size: 41px;
  font-weight: 400;
  font-style: normal;
  letter-spacing: normal;
  line-height: 50px;
  text-align: center;
  text-transform: uppercase;
}

.main-section {
  position: relative;
}

.first-slide {
  height: 100vh;
  width: 100%;
}
.first-slide__inner {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: center;
  width: 100%;
  grid-gap: 134px;
  height: 100vh;
}
@media (min-width: 1900px) {
  .first-slide__inner {
    margin-left: -2.5%;
  }
}
@media (max-width: 767px) {
  .first-slide__inner {
    justify-content: space-between;
    padding-top: 135px;
    padding-bottom: 75px;
  }
}
@media (max-width: 576px) {
  .first-slide__inner {
    padding-top: 85px;
    padding-bottom: 110px;
  }
}
@media (max-width: 576px) {
  .first-slide .main-title {
    text-align: right;
  }
}
@media (max-width: 576px) {
  .first-slide .main-top {
