.default-slide {
  height: 100vh;
  width: 100%;
}
.default-slide__box {
  height: 100vh;
  align-items: center;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-gap: clamp(40px, 5.556vw, 95px);
  position: relative;
}
@media (max-width: 1239px) {
  .default-slide__box {
    grid-template-columns: 1.4fr 1fr;
